<template>
	<div class="list">
      <div class="list-item-top">
        <div class="list-item-top-line">
          <div class="item-left">
            <strong class="item-tit">退款进度</strong>
          </div>
          <div class="item-right">
            <span class="msg-default msg-primay">{{paystatus == 7?'已退款':'退款审核中...'}}</span>
          </div>
        </div>
        <!--<div class="list-item-top-line">-->
          <!--<div class="item-left">-->
            <!--<strong class="item-tit"></strong>-->
          <!--</div>-->
          <!--<div class="item-right">-->
            <!--<span class="msg-time">{{time}}</span>-->
          <!--</div>-->
        <!--</div>-->
      </div>
      <div class="list-item">
        <div class="item-left">
          <strong class="item-tit">退款信息</strong>
        </div>
        <div class="item-right">
          <span class="msg-default">{{curCertName || '无'}}</span>
        </div>
      </div>
      <div class="list-item">
        <div class="item-left">
          <strong class="item-tit">退款金额</strong>
        </div>
        <div class="item-right">
          <span class="msg-default msg-primay">{{price || 0}}元</span>
        </div>
      </div>
      <div class="list-item">
        <div class="item-left">
          <strong class="item-tit">申请时间</strong>
        </div>
        <div class="item-right">
          <span class="msg-default">{{pay_time || '无'}}</span>
        </div>
      </div>
      <div class="list-item">
        <div class="item-left">
          <strong class="item-tit">退款编号</strong>
        </div>
        <div class="item-right">
          <span class="msg-default payorder">{{pay_order || '无'}}</span>
        </div>
      </div>
    </div>

</template>

<script>
export default {
  name: 'Refund',
  data () {
      return {
        openid:'',
        curCertId:'',
        curstatus:'',
        curCertName:'',
        paystatus:'',

        price:'',
        pay_time:'',
        pay_order:''
      }
  },
  created:function(){
    this.getParams();
  },
  methods: {
    getParams(){
      var self = this;
      self.curCertId = self.$route.query.cert_id;
      self.openid = localStorage.openid;
      self.curstatus = self.$route.query.cert_status;
      self.paystatus = self.$route.query.payStatus;
      self.getOrderData();
    },
    getOrderData(){
      let self = this;
      self.$axios.get(self.baseUrl+'/api/v1/weixin/wxquery/'+self.curCertId + '/'+self.openid).then(res =>{
        if(res.data.success){
          let data = res.data.data;
          self.price = data.pay_amount;
          self.curCertName = data.cert_name;
          self.pay_time = self.dateForm(data.pay_time);
          self.pay_order = data.pay_order;
        }else{
          self.$toast.center(res.data.msg);
        }

      })


    },
    //秒数转为时间戳
    dateForm:function(time){
      let date = new Date(time*1000);
      //return date.toLocaleString();
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? ('0' + m) : m;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let minute = date.getMinutes();
      let second = date.getSeconds();
      minute = minute < 10 ? ('0' + minute) : minute;
      second = second < 10 ? ('0' + second) : second;
      return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
    },

   },
   computed: {

   }




}
</script>
<style scoped>
.list{
  width:100%;
  margin-top:20px;
  display: -webkit-flex;
  display: flex;
  padding:40px 30px;
  flex-direction: column;
  background-color:#fff;
  border-top:1px solid #f3f3f5;/*no*/
  padding-top:0;
}
.list-item{
    width:100%;
    height:90px;
    line-height:90px;
    padding:0;
    display: -webkit-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
}
.list-item-top{
  height:120px;
  padding:0;
  border-bottom:1px solid #ddd;/*no*/
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  /*margin-bottom:20px;*/
}
.list-item-top-line{
  width:100%;
  height:120px;
  line-height:120px;
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
}
.msg-time{
  font-size:26px;
  color:#999;
  padding-right:10px;
  letter-spacing: 1.97px;
}
.item-left{

}
.item-right{

}
.item-tit{
    font-size:28px;
    color:#666;
    font-weight: normal;
    padding-left:10px;
}
.msg-default{
    font-size:28px;
    padding-right:10px;
    letter-spacing: 1.97px;
}
.msg-err{
    color:#e83535;
}
.msg-primay{
    color:#d19421;
}
.payorder{
  letter-spacing: 0;
}
.img-cont{
    width:86px;
    height:86px;
    vertical-align: middle;
}
.img-icon{
    width:12px;
    height:21px;
    /*vertical-align: middle;*/

}
img{
    width:100%;
    height:100%;
}
</style>



